.header {
  margin: 10px 50px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between; }
  .header div:nth-of-type(1) {
    font-size: 20px;
    color: salmon; }
  .header .heading {
    padding-left: 10px; }
  .header button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 0 10px 10px 0;
    text-align: right; }
  .header span {
    display: inline-grid; }
  .header svg {
    height: 58px;
    width: 30px;
    display: block; }
  .header .leftContainer button {
    font-size: 20px;
    padding-top: 10px; }

h1 {
  width: 2.6em;
  font-size: 100px;
  text-align: center;
  line-height: 0.8em;
  letter-spacing: -0.05em;
  color: #000;
  text-shadow: -2px -2px 0 #ff6, 2px -2px 0 #ff6, -2px 2px 0 #ff6, 2px 2px 0 #ff6;
  z-index: 1; }

h1 sub {
  display: block;
  font-size: 0.3em;
  letter-spacing: 0;
  line-height: 0.8em; }



